@import url(https://fonts.googleapis.com/css?family=Lato:400,700&display=swap);
html {
  height: 100%;
}

body {
  margin: 0;
  height: 100%;
}

#root {
  height: 100%;
}

.map-container {
  overflow: hidden;
  position: absolute;
  top: 0;
  bottom: 0;
  width: 100%; }
  .map-container .leaflet-container {
    height: 100%;
    background: white; }

.sidebar {
  width: 450px;
  padding: 10px 0 10px 10px;

  -webkit-transition: width 100ms ease-in-out;

  transition: width 100ms ease-in-out;
}

.sidebar.collapsed {
  width: 60px;
}

.sidebar ~ .map-container .leaflet-left {
  -webkit-transition: left 100ms ease-in-out;
  transition: left 100ms ease-in-out;
  left: 450px;
}

.sidebar.collapsed ~ .map-container .leaflet-left {
  left: 60px;
}

